'use client'
import { useEffect, useState } from 'react'
import { supabaseBrowser } from '@/lib/supabaseClient'

type Row = { id: string; image_path: string; description: string; created_at: string }

export default function MySubmissions() {
  const [rows, setRows] = useState<Row[]>([])
  const [urls, setUrls] = useState<Record<string, string>>({})
  const supabase = supabaseBrowser()

  useEffect(() => {
    const fetchData = async () => {
      const { data: { user } } = await supabase.auth.getUser()
      if (!user) { location.href = '/(auth)/login'; return }
      const { data, error } = await supabase
        .from('submissions')
        .select('id,image_path,description,created_at')
        .order('created_at', { ascending: false })
      if (error) { alert(error.message); return }
      setRows(data || [])

      // Create signed URLs for each image (valid 1 hour)
      const paths = (data || []).map(r => r.image_path)
      if (paths.length) {
        const { data: signed } = await supabase
          .storage.from('photos').createSignedUrls(paths, 3600)
        const map: Record<string, string> = {}
        signed?.forEach(s => { if (s?.signedUrl) map[s.path] = s.signedUrl })
        setUrls(map)
      }
    }
    fetchData()
  }, [])

  return (
    <main className="p-6 grid gap-4">
      <h1 className="text-xl font-bold">My Submissions</h1>
      {rows.map(r => (
        <article key={r.id} className="border rounded-xl p-4 grid gap-2">
          {urls[r.image_path] && (
            <img src={urls[r.image_path]} alt="submission" className="w-full rounded-lg" />
          )}
          <p className="whitespace-pre-wrap">{r.description}</p>
          <time className="text-sm opacity-70">
            {new Date(r.created_at).toLocaleString()}
          </time>
        </article>
      ))}
      {!rows.length && <p>No submissions yet.</p>}
    </main>
  )
}
